/**
* CONTENTGROUP
*/

.content_group{
      margin-top: 5rem;
      position: relative;
      z-index: 2;
      width: 100%;
      height: auto;
}

.content_group__page{
      position: relative;
      width: 100%;
      height: auto;
      background: var(--bg-200);
      padding-top: 3rem;
      gap: 2rem;
      border-top: solid .1rem var(--bg-100);
}

.content_group__page:before{
      width: 80%;
      height: 2rem;
      background: #000;
      border-radius: 50%;
      filter: blur(20px);
      left: 10%;
      top: 0;
      z-index: -1;
      opacity: .4;
}

.content_page__section{
      flex: 1;
      height: auto;
      position: relative;
      padding-right: 2rem;
}

.content_page__section:after{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: var(--bg-200);
      border-right: solid .1rem var(--bg-100);
}

.content_page__section:before{
      width: 2rem;
      height: 80%;
      background: #000;
      border-radius: 50%;
      top: 10%;
      right: 0;
      filter: blur(20px);
      opacity: .3;
}

.content_page__section > *{
      position: relative;
      z-index: 1;
}

.content_page__section h2{
      font-size: 2.4rem;
      color: var(--primary);
}

.content_page__description{
      margin-top: 1rem;
}

.content_page__description p{
      font-size: 1.4rem;
      color: var(--text-300);
      white-space: pre-line;
}

.content_social__networks{
      margin-top: 2rem;
      width: 100%;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.content_page__video{
      margin-top: 3rem;
      justify-content: center;
}