/**
* VIDEOS
*/

.video_content{
      flex-direction: column;
      align-items: center;
}

.video_content__box{
      width: 31.7rem;
      height: 18rem;
      border-radius: 1.5rem;
      overflow: hidden;
      background: var(--bg-300);
}

.video_content__box iframe{
      border: none;
}

.video_title{
      margin-top: 1rem;
}

.video_title span{
      color: var(--secundary);
      font-size: 1.4rem;
      text-align: center;
}