/**
* FRIENDS
*/

.content_page__aside{
      width: 21rem;
      flex-shrink: 0;
}

.content_page__aside_title{
      align-items: center;
      gap: .5rem;
      padding-top: 3rem;
}

.content_page__aside_title *{
      font-size: 1.2rem;
      color: var(--secundary);
}

.content_page__aside_title h3{
      color: var(--primary);
      font-size: 1.4rem;
      text-transform: uppercase;
      font-weight: 400;
}

.content_page__friends{
      margin-top: 2rem;
}

.friend_row{
      gap: 1rem;
      height: auto;
      margin-bottom: 1.5rem;
      width: 100%;
}

.friend_row__avatar{
      width: 5.6rem;
      height: 5.6rem;
      position: relative;
      flex-shrink: 0;
      background: var(--bg-300);
      border-radius: 50%;
}

.friend_row__avatar_img{
      width: 8rem;
      height: 8rem;
      background-color: #121212;
      border-radius: 50%;
      overflow: hidden;
      transform: translate(-50%, -50%) scale(.7);
      position: absolute;
      top: 50%;
      left: 50%;
}

@keyframes move_img{
      100%{
            background-position: right top;
      }
}

.friend_row__avatar_img.img{
      display: flex;
      align-items: center;
      justify-content: center;
}

.friend_row__avatar_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.friend_row__data{

}

.friend_row__data strong{
      font-size: 1.4rem;
      text-transform: capitalize;
      font-weight: 500;
}

.friend_row__data p{
      color: var(--secundary);
      font-size: 1.3rem;
}

.friend_row__data a{
      background: var(--bg-200);
      color: var(--text-300);
      text-transform: uppercase;
      font-size: 1rem;
      padding: .4rem 1.2rem;
      border: solid .1rem var(--bg-100);
      border-radius: 999px;
      margin-top: .5rem;
      transition: .2s;
}

.friend_row__data a:hover{
      color: var(--light);
      background: var(--primary);
      border: solid .1rem transparent;
}