/**
* USERTEAM
*/

.user_team{
      width: auto;
      height: 15rem;
      gap: 1rem;
}

.user_team__avatar{
      width: 15rem;
      height: 15rem;
      border-radius: 50%;
      flex-shrink: 0;
      border: solid .2rem var(--primary);
      padding: .4rem;
      position: relative;
}

.user_team__avatar_img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.user_team__avatar_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.user_team__data{
      padding-top: 2rem;
}

.user_team__data h2{
      font-size: 2.8rem;
      text-transform: capitalize;
      line-height: 2.5rem;
}

.user_team__data h2 small{
      font-size: 1.4rem;
      color: var(--primary);
}

.user_team__data p{
      color: var(--secundary);
}

.user_team__data_nav{
      margin-top: 1rem;
      align-items: center;
      gap: 1rem;
}

.user_team__data_nav a{
      width: 3.8rem;
      height: 3.8rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--bg-100);
      border-radius: 50%;
      transition: .2s;
      color: var(--primary);
}

.user_team__data_nav a:hover{
      background: transparent;
      color: var(--light);
}

.user_team__data_nav a *{
      font-size: 2rem;
}

