/**
* ARCBOTDATA
*/

.arcbot_content{
      width: 37rem;
      height: auto;
}

.arcbot_header{
      height: 5.6rem;
      gap: .5rem;
}

.arcbot_header__avatar{
      width: 5.6rem;
      height: 5.6rem;
      padding: .3rem;
      border-radius: .4rem;
}

.arcbot_title{
      font-size: 2.8rem;
      line-height: 2.5rem;
      text-transform: uppercase;
}

.arcbot_header__data{
      padding-top: .5rem;
}

.arcbot_title span{
      font-size: 2.8rem;
      text-transform: initial;
      color: var(--primary);
      font-weight: 400;
}

.arcbot_header__data p{
      text-transform: uppercase;
      font-size: 1.2rem;
}

.arcbot_banner{
      margin-top: 4rem;
      height: 63.5rem;
      width: 100%;
      position: relative;
}

.arcbot_banner__img{
      width: 100%;
      height: 100%;
      background: var(--bg-200);
      border-radius: 1rem;
      overflow: hidden;
      position: relative;
      align-items: center;
      justify-content: center;
}

.arcbot_banner__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.arcbot_banner__front{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(transparent, rgba(0,0,0,.5));
      border-radius: 1rem;
      padding: 1.5rem;
      flex-direction: column;
      justify-content: space-between;
}

.arcbot_banner__front_nav{
      width: 100%;
      height: 4.4rem;
      align-items: center;
      justify-content: space-between;
}

.arcbot_banner__nav{
      gap: 1rem;
}

.arcbot_banner__nav a{
      width: 4.4rem;
      height: 4.4rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,.4);
      border-radius: 50%;
      color: var(--light);
      transition: .2s;
}

.arcbot_banner__nav a:hover{
      color: var(--primary);
}

.arcbot_banner__nav a *{
      font-size: 2rem;
}

.arcbot_banner__dots,
.arcbot_banner__dots:before,
.arcbot_banner__dots:after{
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      border: solid .2rem var(--light);
      box-sizing: border-box;
}

.arcbot_banner__dots{
      position: relative;
}

.arcbot_banner__dots:before,
.arcbot_banner__dots:after{
      position: absolute;
      top: -.2rem;
}

.arcbot_banner__dots:before{
      left: -1.5rem;
}

.arcbot_banner__dots:after{
      left: -3rem;
}

.arcbot_banner__description p{
      font-size: 1.6rem;
      font-weight: 500;
      text-shadow: .2rem .2rem 5px rgba(0,0,0,.6);
}