/**
* SOCIALLINK
*/

.btn_social{
      width: 4rem;
      height: 4rem;
      background: var(--bg-300);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
}

.btn_social *{
      font-size: 2rem;
}

.btn_social.facebook{
      color: var(--facebook);
}

.btn_social.twitter{
      color: var(--twitter);
}

.btn_social.instagram{
      color: var(--instagram);
}

.btn_social.discord{
      color: var(--discord);
}