/**
* ONLINE
*/

.online_content{
      position: absolute;
      bottom: 0;
      right: 0;
      align-items: center;
      justify-content: center;
      gap: .5rem;
      width: 14rem;
      transform: translateX(7.3rem);
}

.online_dots{
      width: 2.6rem;
      height: 2.6rem;
      background: var(--bg-100);
      border-radius: 50%;
      border: solid .4rem var(--bg-200);
      position: relative;
}

.online_dots:before{
      width: 1.2rem;
      height: 1.2rem;
      background: var(--bg-300);
      border-radius: 50%;
      top: calc(50% - .6rem);
      left: calc(50% - .6rem);
}

.online_dots.yellow:before{
      background: var(--bg-300);
}

.online_dots.red:before{
      background: var(--red);
}

.online_dots.green:before{
      background: var(--green);
}

.online_content span{
      font-size: 1.4rem;
      color: var(--light);
}